<script>
import Zi from "../../components/header"
export default {
  data () {
    return {
     data:{},
     geohash:'',
     img:'',
     nowDate:'',
     arr:[],
     indexId:0
    };
  },

  components: {Zi},

  computed: {},
  beforeMount(){
    // if(localStorage.getItem("nameList")){
    //   this.geohash=JSON.parse(localStorage.getItem('nameList')).geohash
    // }
    // console.log( this.geohash)
  },
  mounted(){
     var that=this
this.http.get("http://elm.cangdu.org/shopping/restaurants?latitude=45.763227&longitude=126.595653&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then((d)=>{
    that.arr=d.data.filter((item,index)=>{
        return item.id==that.$route.query.id
   })
   console.log(this.arr)
    
})


    
 var date = new Date();
       var now=new Date();
    
      var hours=now.getHours();
      var minutes=now.getMinutes();
      this.nowDate=hours+':'+(minutes+5)
   
 this.http.get("http://elm.cangdu.org/shopping/restaurant/"+ this.$route.query.id+"?latitude=30.53032&longitude=117.07442&extras[]=activities&extras[]=album&extras[]=license&extras[]=identification&extras[]=statistics").then((d)=>{
   this.img=d.data.image_path
    })
    // this.http.post("http://elm.cangdu.org/v1/carts/checkout",{
    //   come_from:'web',
    //   // geohash:this.geohash,

    // })
  },

  created(){},

  methods: {
    order(){
      this.$router.push("/ding")
      
    },
    tiao(){
      this.$router.push("/address/addressChild")
    }
  }
}

