  text-align: right;
  font-weight: bold;
  color: #ccc;
  }
  display: flex;
  flex-wrap: wrap;
  line-height: 1.5rem;
  border-bottom: 1px dashed #3190e8;
 .info{
   margin-top: 0.3rem;
    p{
    line-height: 0.4rem
  }
 }
  svg{
    margin-left: 0.2rem;
    width: .4rem;
    height: .4rem;
   margin-top: 0.5rem;
    fill: #3190e8;
    margin-right: .1rem;
    }
}
}

</style>


